@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');
@import url('https://fonts.googleapis.com/css?family=Muli:300');

html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  background: -webkit-gradient(linear, right top, left bottom, from(#12082A), to(#12082D));
  background: -webkit-linear-gradient(top right, #12082A, #12082D);
  background: -o-linear-gradient(top right, #12082A, #12082D);
  background: linear-gradient(to bottom left, #12082A, #12082D);
  color: #fff;
}

section {
  padding: 80px 80px 0;
}

h1, h2 {
  font-family: 'Muli', sans-serif;
  font-weight: 300;
  margin: 0;
}

h1 {
  font-size: 2.5em;
}

h2 {
  font-size: 1.5em;
}

p.text-muted {
	opacity: .8;
}

@media (max-width: 768px) {
  section {
    padding: 10px;
  }
}


